:root{
    font-size: 100%;
    font-size: 16px;
    line-height: 1.5;
    --primary-blue: #233975;
}
body{
    padding: 0;
    margin: 0;
    font-family: 'Monterrat', sans-serif;
    font-weight: 500;
}
h1{
    font-size: 1.5rem;
    font-weight: 700;
}
h2{
    font-size: 1.5rem;
    font-weight: 700;
}
a{
    text-decoration: none;
    color: var(--primary-blue);
}
a:hover{
    text-decoration: underline;
}
.small{
    font-size: 80%;
    text-align: center;
}
.split-screen{
    display: flex;
    flex-direction: column;
}
.left{
    height: 200px;
}
.left, .right{
    display: flex;
    justify-content: center;
    align-items: center;
	background-color: rgb(255, 255, 255);
}
.left{
    background: linear-gradient(rgba(0,0,0,0), rgba(0,0,0,0.5)), url('Content.gif');
    background-size: cover;
    position: relative;
}
.left .copy{
    position: absolute;
    top: 0;
    left: 0;
    color: rgb(255, 255, 255);
    text-align: center;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
}
.left .copy h1{
    font-size: 50px;
}
.left p{
    font-weight: 400;
}
.right .copy{
    color: black;
    text-align: center;
}
.right .copy p{
    margin: 1.5em 0;
    font-size: 0.875rem;
}
.right form{
    width: 328px;
}
form input[type="text"], form input[type="email"],form input[type="password"]{
    display: block;
    width: 100%;
    box-sizing: border-box;
    border-radius: 8px;
    border: 1px solid rgb(1, 26, 1);
    padding: 1em;
    margin-bottom: 1.25rem;
    font-size: 0.875rem;
}
label{
    display: block;
    margin-bottom: 0.5rem;
    font-size: 0.75rem;
}
.input-container.password{
    position: relative;
}
.input-container.password i{
    position: absolute;
    top: 42px;
    right:16px;
}


.checkbox-container input{
    position: absolute;
    opacity: 0;
    cursor: pointer;
    height: 0;
    width: 0;
}

.checkbox-container{
    display: block;
    position: relative;
    padding-left: 2rem;
    cursor: pointer;
    user-select: none;
    margin: 0 0 1.25rem 0;
}

.checkbox-container input:checked~.checkmark{
    background-color: var(--primary-blue);
    border: 1px solid var(--primary-blue);
}

.checkmark{
    position: absolute;
    width: 24px;
    height: 24px;
    background:white;
    border: 1px solid #c4c4c4;
    border-radius: 2px;
    top: -4px;
    left: 0;
}
.checkmark:after{
    content:"";
    position: absolute;
    display: none;
}
.checkbox-container input:checked~.checkmark:after{
    display: block;
}
.checkbox-container .checkmark:after{
    border: solid white;
    border-width: 0 3px 3px 0;
    width: 5px;
    height: 10px;
    left: 4px;
    transform: rotate(45deg);
}
.login-btn {
    display: block;
    width: 100%;
    background: rgb(6, 134, 185);
    color: rgb(255, 255, 255);
    font-weight: 700;
    border:none;
    padding: 1rem;
    border-radius: 8px;
    font-size: 0.75rem;
    text-transform: uppercase;
    letter-spacing: 0.5px;
}
.login-btn a{
	color: rgb(255, 255, 255);
}
.login-btn:hover{
    background: #065a6e;
    cursor: pointer;
}

@media screen and (min-width: 700px) {
    .split-screen{
        flex-direction: row;
        height: 100vh;
    }
    .left, .right {
        display: flex;
        width: 50%;
        height: auto;
    }
}