:root{
    font-size: 100%;
    font-size: 16px;
    line-height: 1.5;
    --primary-blue: #233975;
}
body{
    padding: 0;
    margin: 0;
    font-family: 'Monterrat', sans-serif;
    font-weight: 500;
}
.left{
    background: linear-gradient(rgba(0,0,0,0), rgba(0,0,0,0.5)), url(Content.gif);
    background-size: cover;
    position: relative;
}
.left .copy{
    position: absolute;
    top: 0;
    left: 0;
    color: rgb(255, 255, 255);
    text-align: center;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
}
.left .copy h1{
    font-size: 50px;
}
.left p{
    font-weight: 400;
}
h1{
    font-size: 1.5rem;
    font-weight: 700;
}
h2{
    font-size: 1.5rem;
    font-weight: 700;
}
a{
    text-decoration: none;
    color: var(--primary-blue);
}
a:hover{
    text-decoration: underline;
}
.small{
    font-size: 80%;
    text-align: center;
}
.split-screen{
    display: flex;
    flex-direction: column;
}
.left{
    height: 200px;
}
.left, .right{
    display: flex;
    justify-content: center;
    align-items: center;
	background-color: rgb(255, 255, 255);
}

.right {
    background-color: #f2f2f2;
    padding: 20px;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
}
.right .copy{
    color: black;
    text-align: center;
}
.right .copy p{
    margin: 1.5em 0;
    font-size: 0.875rem;
}
.right form{
    width: 328px;
}

.form {
    display: flex;
    flex-direction: column;
}

.input-container {
    display: flex;
    flex-direction: column;
    margin-bottom: 20px;
}

label {
    font-size: 14px;
    font-weight: 600;
    margin-bottom: 5px;
}

input[type="email"] {
    border: 1px solid #ccc;
    border-radius: 4px;
    height: 40px;
    padding: 10px;
    font-size: 14px;
}

button.login-btn {
    background-color: #007bff;
    color: #fff;
    border: none;
    border-radius: 4px;
    font-size: 16px;
    font-weight: 600;
    padding: 10px 20px;
    cursor: pointer;
    transition: background-color 0.2s ease;
}
.login-btn a{
	color: rgb(255, 255, 255);
}

button.login-btn:hover {
    background-color: #0062cc;
}


@media screen and (min-width: 700px) {
    .split-screen{
        flex-direction: row;
        height: 100vh;
    }
    .left, .right {
        display: flex;
        width: 50%;
        height: auto;
    }
}